@import "colors";

// rollup-plugin-inject@3.0
footer {
    background-color: $first;
    height: 200px;
    width: 100%;
    color: $dark;
    display: grid; //grid le garda deutai div chutyo
    grid-template-columns: 4fr 1fr; //yo buhje na ma
    padding: 2rem; //side bata space

    >div {
        display: flex;
        flex-direction: column;
        justify-content: center;

        >h1 {
            font-size: 700 2rem $text1;
            color: $third;
        }

        >p {
            font: 500 1.2rem $text1;
        }

        >h5 {
            font: 300 2rem $text1;
            margin: 1rem 0;
            text-align: center;
        }

        >div {
            display: flex;
            flex-direction: column;
            align-items: center;

            a {
                color: $dark;
                font: 900 1rem $text2;

                &:hover {
                    color: $third;
                }
            }
        }

    }
}