@import "colors";

@media screen and (max-width: 820px) {
    .home3 {
        height: 130vh;

        >div {
            height: 120vh;
        }
    }

    .contact>main {
        height: 100vh;
        width: 100%;
        border-radius: 0%;

        h1 {
            text-align: center;
        }
    }
}


@media screen and (max-width: 700px) {
    nav {
        >h1 {
            display: none;
        }

        >main {
            width: 100%;
            justify-content: center;

            a {
                margin: 0.5rem;
            }
        }
    }

    .home>main {
        align-items: center;

        h1 {
            // margin-top: 3px;
            font-size: 3rem;
        }
    }

    .home2 {
        height: 120vh;

        flex-direction: column;



        img {
            width: 100%;
        }

        >div {
            width: 100%;

            >p {
                padding: none;
            }
        }
    }

    .home3 {
        height: 120vh;
        // border: 1px solid red;

        >div {
            text-align: center;
            width: 100%;
            border-radius: 0;
            overflow: hidden;
            padding-top: 90px;

            >p {
                padding: 6px 2px 6px 2px;
                font-size: 0.9rem;
                line-height: 150%;
                text-align: left;
            }
        }
    }


    .home4 {
        align-items: center;

        >div {
            border-radius: 0%;
            width: 100%;
            height: unset;
        }
    }



    .contact>main {
        padding: 1rem;

        h1 {
            margin: 1rem;

        }

        >form {
            margin: 0%;

            >div {
                width: 100%;
            }
        }
    }

    footer {
        margin-top: 200px;
        margin-bottom: 60px;
        height: 50vh;
        grid-template-columns: 1fr;
        padding: 5rem;
        // background-color: rgb(114, 138, 159);

        >div {
            align-items: center;
        }
    }
}