@import "colors";

nav {
    width: 100%;
    padding: 1rem; //paddin- space from every side
    display: flex; //flex brings all things in  a row
    justify-content: space-between;
    align-items: center;
    position: sticky;
    background-color: $first;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2);
    top: 0%;
    z-index: 10;
    // border: 1px solid black;

    h1 {
        text-transform: uppercase;
    }

    main {
        width: 70%;
        display: flex;
        justify-content: flex-end;
        // border: 1px solid rebeccapurple;

        a {
            color: $dark;
            margin: 1rem;


            &:hover {
                color: $third;
            }
        }
    }
}